import React from 'react';

import { isMobile, isTablet } from 'react-device-detect';
import { Button, Card, Nav } from 'react-bootstrap';
import { Line } from "react-chartjs-2";

import DataSource from './DataSource';

class ContentCard extends DataSource {
  period = '1y';

  constructor(props) {
    super(props);

    this.handle3Month = this.handle3MonthButton.bind(this);
    this.handle6Month = this.handle6MonthButton.bind(this);
    this.handle1Year = this.handle1YearButton.bind(this);
    this.handle3Year = this.handle3YearButton.bind(this);
    this.handle5Year = this.handle5YearButton.bind(this);
    this.handle10Year = this.handle10YearButton.bind(this);
    this.handleMax = this.handleMaxButton.bind(this);
  }

  getValues() {
    var indices = [];
    var values_1 = [];
    var values_2 = [];
    for (var i = 0; i < 10; i++) {
      indices.push(i.toString());
      values_1.push((Math.random() * 50).toFixed(2));
      values_2.push((Math.random() * 50).toFixed(2));
    }

    return [indices, values_1, values_2];
  }

  static defaultProps() {
    return {
      isPeriodShown: 'true'
    }
  }

  getData() {
    var values = this.getValues();
    if (values[0].length === 0) {
      return null;
    }

    const data = {
      labels: values[0],
      datasets: [
        {
          label: '',
          data: values[1],
          fill: false,
          backgroundColor: '#1e6eff',
          borderColor: '#1e6eff',
          borderWidth: 1.5,
          pointRadius: 0,
          hitRadius: 5,
          cubicInterpolationMode: 'monotone',
          lineTension: 0.1,
          spanGaps: true
        }
      ]
    };

    return data    
  }

  render() {
    var chartFontSize = 16;
    if (isTablet);
    if (isMobile) chartFontSize = 8;

    let line;
    let data = this.getData();
    if (data != null) {
      line =
        <Line
          data={data}
          height={150}
          options={{
            legend: {
              display: false
            },
            scales: {
              xAxes: [{
                gridLines: {
                  drawBorder: true,
                  drawOnChartArea: true,
                  lineWidth: 0.0,
                  zeroLineWidth: 0.5
                },
                ticks: {
                  fontSize: chartFontSize,
                  autoSkip: true,
                  maxTicksLimit: 4,
                  minRotation: 0,
                  maxRotation: 0
                }
              }],
              yAxes: [{
                ticks: {
                  fontSize: chartFontSize
                },
                gridLines: {
                  drawOnChartArea: true,
                  lineWidth: 0.2,
                  zeroLineWidth: 0.5
                }
              }]
            },
            animation: {
              duration: 0
            }
        }}></Line>
    } else {
      line = <h6 style={{ color:'#333333' }}>데이터 로딩 중...</h6>
    }

    let period;
    if (this.props.isPeriodShown === 'true' || this.props.isPeriodShown === undefined) {
      period =
        <Nav
          defaultActiveKey='1y'
          onSelect={(selectedKey) => this.setState({ period: selectedKey }) }
        >
          <Nav.Item><Nav.Link eventKey="3m" style={{color:'#666666'}}>3m</Nav.Link></Nav.Item>
          <Nav.Item><Nav.Link eventKey="6m" style={{color:'#666666'}}>6m</Nav.Link></Nav.Item>
          <Nav.Item><Nav.Link eventKey="1y" style={{color:'#666666'}}>1y</Nav.Link></Nav.Item>
          <Nav.Item><Nav.Link eventKey='3y' style={{color:'#666666'}}>3y</Nav.Link></Nav.Item>
          <Nav.Item><Nav.Link eventKey="5y" style={{color:'#666666'}}>5y</Nav.Link></Nav.Item>
          <Nav.Item><Nav.Link eventKey="10y" style={{color:'#666666'}}>10y</Nav.Link></Nav.Item>
          <Nav.Item><Nav.Link eventKey="Max" style={{color:'#666666'}}>Max</Nav.Link></Nav.Item>
        </Nav>
    }

    var style_card_title = { marginTop:'0.15rem', marginBottom:'0.5rem', marginLeft:'0.15rem'};
    if (isTablet);
    if (isMobile) style_card_title = { fontSize:'12px', marginTop:'0.15rem', marginBottom:'0.5rem', marginLeft:'0.15rem'};

    var style_period_button = { fontSize:'16px', height:'25px', lineHeight:'12px', textAlign:'center' };
    if (isTablet);
    if (isMobile) style_period_button = { fontSize:'6px', height:'20px', lineHeight:'6px', textAlign:'center' };

    return (
        <Card style={{ width:'96%', marginBottom:'10px', marginLeft:'2%' }}>
          <Card.Body style={{ padding:'0.75em'}}>
            <Card.Title style={style_card_title}>{this.props.title}</Card.Title>
            <Card.Text style={{ color:'#999999', marginLeft:'0.15rem', marginBottom:'1.0rem' }}>{this.props.text}</Card.Text>
            <Card.Text></Card.Text>
            { line }
            <div style={{ width:'100%', height:'20px' }}></div>
            <center>
            {/* period */}
            <>
              <Button variant="light" style={style_period_button} onClick={this.handle3Month}>3개월</Button>{' '}
              <Button variant="light" style={style_period_button} onClick={this.handle6Month}>6개월</Button>{' '}
              <Button variant="light" style={style_period_button} onClick={this.handle1Year}>1년</Button>{' '}
              <Button variant="light" style={style_period_button} onClick={this.handle3Year}>3년</Button>{' '}
              <Button variant="light" style={style_period_button} onClick={this.handle5Year}>5년</Button>{' '}
              <Button variant="light" style={style_period_button} onClick={this.handle10Year}>10년</Button>{' '}
              <Button variant="light" style={style_period_button} onClick={this.handleMax}>최대</Button>{' '}
            </>
            </center>
            <div style={{ width:'100%', height:'10px' }}></div>
          </Card.Body>
        </Card>
    )
  }

  handle3MonthButton() { this.setState({ period: '3m' }); }
  handle6MonthButton() { this.setState({ period: '6m' }); }
  handle1YearButton() { this.setState({ period: '1y' }); }
  handle3YearButton() { this.setState({ period: '3y' }); }
  handle5YearButton() { this.setState({ period: '5y' }); }
  handle10YearButton() { this.setState({ period: '10y' }); }
  handleMaxButton() { this.setState({ period: 'Max' }); }
}

export default ContentCard;